:root {
  --font-body: "Poppins", sans-serif;
}

html,
body {
  font-family: "Poppins", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --blue: hsl(207, 100%, 37%);
  --light-blue: hsl(201, 100%, 47%);
  --red: hsl(14, 87%, 58%);
  --body-text: hsl(0, 2%, 38%);
  --title-text: hsl(219, 100%, 8%);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  transition: all 250ms;
}

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

html, body {
  overflow-x: hidden;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.4;
  position: relative;
  color: #616161;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3 {
  margin: 0;
  line-height: 1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.section__title {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  margin: 0.3em auto;
  background: linear-gradient(130deg, #00a6ff, #0085cc, #005499, #0085cc, #00a6ff);
  background-size: 200% auto;
  color: var(--blue);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 5s linear infinite;
}
@keyframes shine {
  to {
    background-position: 200% center;
  }
}
@media (min-width: 40em) {
  .section__title {
    font-size: 2.5em;
  }
}

.section__description {
  font-size: 1rem;
  margin: 0.3em auto;
  text-align: center;
}

.container {
  width: 85%;
  max-width: 75rem;
  margin: auto;
}

.navbar {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "ham logo ." "link link link";
  align-items: center;
  padding: 0.3em 1em;
}
.navbar .hamburger {
  grid-area: ham;
  width: 25px;
  height: 3px;
  background: var(--title-text);
  border-radius: 100px;
  transition: all 250ms;
}
.navbar .hamburger::after, .navbar .hamburger::before {
  display: block;
  content: "";
  background: var(--title-text);
  height: 3px;
  border-radius: 100px;
  width: 115%;
  transition: all 250ms;
}
.navbar .hamburger::after {
  transform: translateY(-11px);
}
.navbar .hamburger::before {
  transform: translateY(8px);
}
.navbar .nav-logo {
  grid-area: logo;
}
.navbar .nav-links {
  display: block;
  grid-area: link;
  list-style-type: none;
  text-align: center;
  padding-left: 0;
  max-height: 0;
  overflow: hidden;
  transition: all 250ms;
  margin-bottom: -0.5em;
}
.navbar .nav-links .nav__link {
  color: var(--blue);
  font-weight: 600;
  padding: 1em 0;
  display: block;
  border-bottom: 1px solid rgba(0, 156, 240, 0.2);
  transition: all 250ms;
}
.navbar .nav-links .nav__link:hover {
  color: var(--red);
  border-bottom: 1px solid var(--red);
  transition: all 250ms;
}
@media (min-width: 40em) {
  .navbar {
    grid-template-areas: ". logo ." "link link link";
  }
  .navbar .nav-logo {
    text-align: center;
  }
  .navbar .nav-logo img {
    width: 40%;
    margin: auto;
  }
  .navbar .hamburger {
    display: none;
  }
  .navbar .nav-links {
    max-height: 1000px;
    padding: 0;
    margin: 0.7em auto;
  }
  .navbar .nav-links .nav__link {
    display: inline-block;
    padding: 0.5em 1em;
  }
}
@media (min-width: 71.875em) {
  .navbar .navbar {
    display: flex;
  }
}

